import React from 'react';
import AppBar from '@mui/material/AppBar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import Toolbar from '@mui/material/Toolbar';
import { Box } from '@mui/material';
import { Link } from 'react-router-dom';
import './style.css';
import { makeStyles } from '@mui/styles';
import { useSelector } from 'react-redux';

const pages = [
    {
        link: 'comments',
        label: 'Comments',
    },
    {
        link: 'sign-in',
        label: 'Sign In',
        class: '',
    },
    {
        link: 'sign-up',
        label: 'Register',
        class: '',
    },
];
const useStyles = makeStyles(() => ({
    toolBar: {
        margin: 'auto',
        maxWidth: 800,
        width: '100%',
    },
}));
const Header = () => {
    const classes = useStyles();
    const appState = useSelector((state) => state);
    const { signInByPasswordReducer } = appState as any;
    let username = '';
    if (signInByPasswordReducer && signInByPasswordReducer.data && signInByPasswordReducer.data.username) {
        username = signInByPasswordReducer.data.username;
    }

    return (
        <React.Fragment>
            <CssBaseline />
            <AppBar position="static" className="appHeader" sx={{ backgroundColor: '#226C7C' }}>
                <Toolbar sx={{ backgroundColor: '#226C7C' }} className={classes.toolBar}>
                    <Box
                        component="img"
                        sx={{
                            height: 30,
                            width: 30,
                            marginRight: '100px',
                        }}
                        alt="TestAssignment"
                        src="mainLogo.png"
                    />

                    <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' }, backgroundColor: '#226C7C' }}>
                        {pages.map((page: any, index: number) => (
                            <Button key={index} sx={{ my: 2, color: 'white', display: 'block' }}>
                                <Link
                                    to={page.link}
                                    style={{ color: '#fff', textTransform: 'capitalize', textDecoration: 'none' }}
                                    className={page.class}
                                >
                                    {page.label}
                                </Link>
                            </Button>
                        ))}
                        {username ? (
                            <>
                                <Button key={'logout'} sx={{ my: 2, color: 'white', display: 'block' }}>
                                    <Link to="">Logout</Link>
                                </Button>
                                <Button key={username} sx={{ my: 2, color: 'white', display: 'block' }}>
                                    <Link to="">Welcome: {username}</Link>
                                </Button>
                            </>
                        ) : (
                            ''
                        )}
                    </Box>
                </Toolbar>
            </AppBar>
        </React.Fragment>
    );
};

export default Header;
